/*animation*/
#animation-grid-1{
  background-color: rgb(172, 212, 62);
  height: 40%;
  width: 2px;
position: fixed;
right: 20px;
bottom:-40%;
z-index: 9999;
}

#animation-grid-2{
  background-color: rgb(184, 181, 181);
  height: 30%;
  width: 2px;
position: fixed;
right: 27px;
bottom:-30%;
z-index: 9999;
}

#animation-grid-3{
  background-color: rgb(124, 121, 121);
  height: 2px;
  width: 40%;
position: fixed;
right: -40%;
bottom:20px;
z-index: 9999;
}

#animation-grid-4{
  background-color: rgb(172, 212, 62);
  height: 2px;
  width: 30%;
position: fixed;
right: -40%;
bottom:28px;
z-index: 9999;
}


